
.mainTitle{
  text-align: center;
}
#jb-container {
  width: 85%;
  margin: 0 auto;
}
#jb-header {
  margin : 0 auto;
}
#jb-content {
  width: 60%;
  float: left;
}
#jb-sidebar {
  width: 20%;
  float: left;
}
#jb-nav {
  width: 20%;
  float: left;
}
#jb-footer {
  clear: both;
}
#jb-cmdContainer {
  overflow: auto;
  text-align: center;
  /* height: ; */
}
.contain_wrap {
  vertical-align: center;
  height: 50px;
}
.cmdInput{
  background-color:  #000000;
  color:#ffffff;
  border-color: #000000;
  margin-left: 10%
}
.cmdTitle{
  display: inline-block;
  padding:0;
  margin: 0;
}
@media ( max-width: 480px ) {
  #jb-container {
    width: auto;
  }
  #jb-content {
    float: none;
    width: auto;
  }
  #jb-sidebar {
    float: none;
    width: auto;
  }
  #jb-nav {
    float: none;
    width: auto;
  }
}